.neon-checkbox {
  --primary: #0fa;
  --primary-dark: #0c8;
  --primary-light: #8fd;
  --size: 30px;
  width: var(--size);
  height: var(--size);
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  position: relative;
}

.neon-checkbox input {
  display: none;
}

.neon-checkbox__frame {
  width: 100%;
  height: 100%;
  position: relative;
}

.neon-checkbox__box {
  border: 2px solid var(--primary-dark);
  background: #000c;
  border-radius: 4px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.neon-checkbox__check-container {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 2px;
}

.neon-checkbox__check {
  fill: none;
  width: 80%;
  height: 80%;
  stroke: var(--primary);
  stroke-width: 3px;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 40;
  stroke-dashoffset: 40px;
  transform-origin: center;
  transition: all .4s cubic-bezier(.16, 1, .3, 1);
}

.neon-checkbox__glow {
  background: var(--primary);
  opacity: 0;
  filter: blur(8px);
  border-radius: 6px;
  transition: all .4s;
  position: absolute;
  inset: -2px;
  transform: scale(1.2);
}

.neon-checkbox__borders {
  border-radius: 4px;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.neon-checkbox__borders span {
  background: var(--primary);
  opacity: 0;
  width: 40px;
  height: 1px;
  transition: opacity .4s;
  position: absolute;
}

.neon-checkbox__borders span:first-child {
  animation: 2s linear infinite borderFlow1;
  top: 0;
  left: -100%;
}

.neon-checkbox__borders span:nth-child(2) {
  width: 1px;
  height: 40px;
  animation: 2s linear infinite borderFlow2;
  top: -100%;
  right: 0;
}

.neon-checkbox__borders span:nth-child(3) {
  animation: 2s linear infinite borderFlow3;
  bottom: 0;
  right: -100%;
}

.neon-checkbox__borders span:nth-child(4) {
  width: 1px;
  height: 40px;
  animation: 2s linear infinite borderFlow4;
  bottom: -100%;
  left: 0;
}

.neon-checkbox__particles span {
  background: var(--primary);
  opacity: 0;
  pointer-events: none;
  width: 4px;
  height: 4px;
  box-shadow: 0 0 6px var(--primary);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
}

.neon-checkbox__rings {
  pointer-events: none;
  position: absolute;
  inset: -20px;
}

.neon-checkbox__rings .ring {
  border: 1px solid var(--primary);
  opacity: 0;
  border-radius: 50%;
  position: absolute;
  inset: 0;
  transform: scale(0);
}

.neon-checkbox__sparks span {
  background: linear-gradient(90deg, var(--primary), transparent);
  opacity: 0;
  width: 20px;
  height: 1px;
  position: absolute;
}

.neon-checkbox:hover .neon-checkbox__box {
  border-color: var(--primary);
  transform: scale(1.05);
}

.neon-checkbox input:checked ~ .neon-checkbox__frame .neon-checkbox__box {
  border-color: var(--primary);
  background: #00ffaa1a;
}

.neon-checkbox input:checked ~ .neon-checkbox__frame .neon-checkbox__check {
  stroke-dashoffset: 0;
  transform: scale(1.1);
}

.neon-checkbox input:checked ~ .neon-checkbox__frame .neon-checkbox__glow {
  opacity: .2;
}

.neon-checkbox input:checked ~ .neon-checkbox__frame .neon-checkbox__borders span {
  opacity: 1;
}

.neon-checkbox input:checked ~ .neon-checkbox__frame .neon-checkbox__particles span {
  animation: .6s ease-out forwards particleExplosion;
}

.neon-checkbox input:checked ~ .neon-checkbox__frame .neon-checkbox__rings .ring {
  animation: .6s ease-out forwards ringPulse;
}

.neon-checkbox input:checked ~ .neon-checkbox__frame .neon-checkbox__sparks span {
  animation: .6s ease-out forwards sparkFlash;
}

@keyframes borderFlow1 {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(200%);
  }
}

@keyframes borderFlow2 {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(200%);
  }
}

@keyframes borderFlow3 {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-200%);
  }
}

@keyframes borderFlow4 {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-200%);
  }
}

@keyframes particleExplosion {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%)scale(1);
  }

  20% {
    opacity: 1;
  }

  100% {
    transform: translate(calc(-50% + var(--x, 20px)), calc(-50% + var(--y, 20px))) scale(0);
    opacity: 0;
  }
}

@keyframes ringPulse {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(2);
  }
}

@keyframes sparkFlash {
  0% {
    transform: rotate(var(--r, 0deg)) translateX(0) scale(1);
    opacity: 1;
  }

  100% {
    transform: rotate(var(--r, 0deg)) translateX(30px) scale(0);
    opacity: 0;
  }
}

